.right-div {
    background: transparent;
    background-color: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(31px);
    border-radius: 10px;
    color: white;
    align-self: flex-end;
    display: flex;
    width: 350px;
    flex-direction: column;
    text-align: center;
    margin-left: 50px;
}
.right-div .strong {
    font-weight: 900;
}
.right-div {
    vertical-align: bottom;
    position: absolute;
    top: 65%;
}
.container {
    position: relative;
    align-self: flex-start;
}
.container .right-img {
    position: relative;
    width: 650px;
    height: auto;
}
.first {
    text-align: center;
    display: flex;
    justify-content: space-around;
    flex-direction: row;
}
.second {
    text-align: center;
    display: flex;
    justify-content: space-around;
}
.person-div {
    display: flex;
    text-align: center;
}
.second .yellow {
    color: #D0A920;
}
@media screen and (max-width: 1100px) {
    .container {
        display: none;
    }
}
