body {
    background-image: url("../assets/Ellipse\ 3.svg");
    background-repeat: no-repeat;
    background-size: 500px auto;
    background-position:right bottom;
    background-color: #0C172C;
    width: 100%;
    display: grid;
    /* overflow: hidden; */
}
.parent {
    display: flex;
    justify-content: space-around;
}
.form-container h1 {
    color: white;
    font-size: 2.04rem;
    text-align: center;
}
u {
    text-decoration-color: #D0A920;
}
.form-container p {
    font-size: 1rem;
}
.form-container {
    width: 600px;
    margin-right: 1rem;
    color: white;
    align-self: center;
}
form {
    display: flex;
    flex-direction: column;
    align-items: center;
}
input[type=text], input[type=password] {
    display: flex;
    border: none;
    background: #0C172C;
    padding: 15px;
    font-size: 0.8rem;
    color: rgb(102, 102, 102);
}
input:focus {
    outline: none;
}
a {
    text-decoration: none;
    color: white;
}
.sign a {
    cursor: pointer;
    border: none;
    background: none;
    text-decoration: none;
    color: #D0A920;
}
.sign a:hover {
    color: #ba981a;
}
.registerbtn {
    color: #D0A920;
    background-color: #27395B;
    border: none;
    text-align: center;
    width: 70%;
    padding-top: 25px;
    padding-bottom: 25px;
    border-radius: 10px;
    cursor: pointer;
    font-size: 1rem;
}
.registerbtn:hover {
    background-color: #203356;
}
.another-stupid-div {
    display: flex;
    width: 80%;
    align-items: center;
    margin-top: 15px; 
}

.message {
    background-color: black;
    color: white;
    width: fit-content;
    padding: 1rem;
    border-radius: 10px;
    margin: auto;
}
form span {
    display: flex;
    margin-bottom: 0.8rem;
    font-size: 0.8rem;
    color: red;
}
form p .btn {
    width: 24px;
    height: auto;
    color: #D0A920;
    cursor: pointer;
}
form .fa-solid {
    color: grey;
    width: 20px;
    height: auto;
}
.test-div {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid grey;
    width: 80%;
    align-items: center;
}
@media screen and (max-width: 1100px){
    body {
        background-image: url("../assets/Ellipse\ 3.svg");
        background-repeat: no-repeat;
        background-size: 300px auto;
        background-position: right bottom;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        left: 0;
        width: 100%;
        overflow: visible;
    }
    .form-container {
        width: 100%;
        margin-right: 0;
    }
    .test-div {
        border-bottom: 1px solid grey;
    }
}